import React from "react";
import styled from "styled-components";
import { useState } from "react";
import Modal from "./Modal";

function PorQueEspaa(props) {
  const [economia, setEconomia] = useState(false);
  const [impuestos, setImpuestos] = useState(false);
  const [talento, setTalento] = useState(false);
  const [infraestructura, setInfraestructura] = useState(false);
  const [calidadDeVida, setCalidadDeVida] = useState(false);

  return (
    <>
      <sv xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
  <path fill="#0099ff" fill-opacity="1" d="M0,96L48,90.7C96,85,192,75,288,69.3C384,64,480,64,576,53.3C672,43,768,21,864,16C960,11,1056,21,1152,26.7C1248,32,1344,32,1392,32L1440,32L1440,0L1392,0C1344,0,1248,0,1152,0C1056,0,960,0,864,0C768,0,672,0,576,0C480,0,384,0,288,0C192,0,96,0,48,0L0,0Z"></path>
</sv>
<PorQueEspaaContainer id="por-que-espaa">
        <h2>POR QU ESPAA?</h2>
        <div id="items">
          <div onClick={() => setEconomia(true)}>
            <img src={props.economia} />
            <h4>Economia</h4>
            <span>ver ms</span>
          </div>

          <div onClick={() => setImpuestos(true)}>
            <img src={props.impuestos} />
            <h4>Impuestos</h4>
            <span>ver ms</span>
          </div>

          <div onClick={() => setTalento(true)}>
            <img src={props.talento} />
            <h4>Talento</h4>
            <span>ver ms</span>
          </div>

          <div onClick={() => setInfraestructura(true)}>
            <img src={props.infraestructura} />
            <h4>Infraestructura</h4>
            <span>ver ms</span>
          </div>

          <div onClick={() => setCalidadDeVida(true)}>
            <img src={props.calidadDeVida} />
            <h4>Calidad de Vida</h4>
            <span>ver ms</span>
          </div>
        </div>
        {/* Modal #1 */}
        <Modal value="Economia" state={economia} setState={setEconomia}>
          <Content>
            <p>
              Espaa, una de las principales economas europeas, ha superado de
              forma sostenida a la de sus homlogos europeos durante los ltimos
              cinco aos, en circunstancias previas a la pandemia.
              <br />
              <br />
              Con un PIB de 1.200 millones de dlares, Espaa es la 4 economa
              de la UE -salvo la del Reino Unido- y la 14 del mundo.
              <br />
              <br />
              Espaa es el 13 receptor de inversiones extranjeras en el mundo.
              <br />
              Ms de 14.600 empresas extranjeras han instalado su negocio en
              Espaa. 70 de las 100 principales empresas de Forbes Global 2000
              operan en Espaa.
              <br />
              <br />
              El sector empresarial espaol est muy internacionalizado. Las
              asociaciones con empresas locales ofrecen excelentes oportunidades
              para ingresar a otros mercados.
              <br />
              <br />
              Espaa es el 11 mayor exportador mundial de servicios
              comerciales, registrado en 115.000 millones de dlares.
              <br />
              <br />
              El ndice de Restriccin Regulatoria de la IED de la OCDE sita a
              Espaa como el 10 pas del mundo con las regulaciones menos
              restrictivas para las inversiones internacionales.
              <br />
              <br />
              Segundo destino turstico despus de Francia en nmero de
              visitantes anuales. Lidera el mundo en competitividad turstica
              segn el Foro Econmico Mundial en 2019.
            </p>
          </Content>
        </Modal>
        {/* Modal #2 */}
        <Modal value="Talento" state={impuestos} setState={setImpuestos}>
          <Content>
            <p>
              La tasa del impuesto de sociedades de Espaa dentro de la Eurozona
              es moderada (25%) y los incentivos y exenciones fiscales pueden
              reducir la tasa impositiva efectiva a alrededor del 20%.
              <br />
              <br />
              Hasta el 42% de bonificacin en el Impuesto sobre Sociedades por
              actividades de I+D+i.
              <br />
              <br />
              En caso de insuficiencia del IRPF, la normativa espaola permite
              trasladar a inversiones de I+D+i los crditos fiscales no
              utilizados.
              <br />
              <br />
              Espaa es una plataforma para negocios internacionales y
              oportunidades de inversin. El sistema fiscal espaol es ventajoso
              para las empresas internacionales que pueden beneficiarse de los
              94 convenios de doble imposicin suscritos por Espaa y
              actualmente en vigor, en particular con pases de Amrica Latina.
              <br />
              <br />
              El esquema de exencin de participacin beneficia a las empresas
              internacionales. Los dividendos y ganancias patrimoniales
              obtenidos por la transmisin de acciones de filiales no residentes
              en Espaa estn exentos de tributacin.
              <br />
              <br />
              Espaa cuenta con un rgimen fiscal especial para las sociedades
              holding (Spanish Holding Companies -ETVE) que permite que los
              dividendos y las ganancias patrimoniales procedentes de la
              transmisin de acciones de filiales estn exentos de tributacin.
              <br />
              <br />
              En Espaa existe un rgimen de neutralidad fiscal para las
              operaciones de reestructuracin empresarial por lo que las
              fusiones, escisiones o cualquier otra reorganizacin estructural
              pueden realizarse sin sanciones fiscales.
              <br />
              <br />
              El tipo fijo del IRPF para los seis primeros aos es del 24% sobre
              todas las rentas hasta 600.000, y del 47% sobre las rentas
              superiores a 600.000 si el expatriado no ha residido en Espaa
              durante los diez perodos fiscales anteriores a su traslado al
              pas , en cuyo caso, los rendimientos del ahorro obtenidos en el
              extranjero no tributan en Espaa.
            </p>
          </Content>
        </Modal>
        {/* Modal #3 */}
        <Modal value="Talento" state={talento} setState={setTalento}>
          <Content>
            <p>
              Espaa es el sexto pas del mundo y el tercero de Europa en
              porcentaje de poblacin con estudios superiores (Informe GTCI
              2020, INSEAD).
              <br />
              <br />
              Este es un gran momento para el Emprendimiento en Espaa. Con el
              apoyo de polticas e incentivos nacionales y regionales, este
              aumento de nuevas iniciativas empresariales promueve diversas
              formas de alianzas relacionadas con el trabajo.
              <br />
              <br />
              Espaa es una sociedad abierta e inclusiva. Ms de 6 millones de
              extranjeros viven en el territorio espaol (el 13% de la
              poblacin) y estn bien integrados en la sociedad. Espaa es
              particularmente atractiva para el talento de habla hispana.
              <br />
              <br />
              Espaa es el destino preferido de los estudiantes universitarios
              en el programa de intercambio Erasmus segn la Comisin Europea:
              principalmente por el idioma, la calidad de sus universidades y su
              cultura abierta e inclusiva hacia los extranjeros.
            </p>
          </Content>
        </Modal>
        {/* Modal #4*/}
        <Modal
          value="Infraestructura"
          state={infraestructura}
          setState={setInfraestructura}
        >
          <Content>
            <p>
              50 aeropuertos, la mayora con conexiones internacionales
              directas. Dos de los aeropuertos ms concurridos de Europa: Madrid
              y Barcelona. Vuelos directos a cuatro continentes, 74 pases y 189
              ciudades.
              <br />
              <br />
              La mayor red de autopistas y autopistas de la UE (ms de 17.000
              km).
              <br />
              <br />
              La tercera red ferroviaria de alta velocidad ms extensa del mundo
              (3.402 kilmetros). Las mejores valoraciones a nivel mundial por
              la calidad de su sistema ferroviario, carreteras y servicios
              aeroportuarios.
              <br />
              <br />
              Espaa tiene la tasa de penetracin de FTTH (fibra hasta el hogar)
              ms alta de todos los pases de la Unin Europea. Adems, la banda
              ancha ultrarrpida cubre el 87% del pas, frente al 60% en Europa.
              <br />
              <br />
              El informe DESI (Digital Economy and Society Index) de 2019 sita
              a Espaa en el octavo lugar entre los pases mejor preparados para
              el despliegue de la tecnologa 5G, al haber subastado el 30% de su
              espectro de frecuencias frente al 14% de media en la UE.
              <br />
              <br />
              Espaa es tambin uno de los pases en realizar ms pruebas, con
              proyectos piloto en todo el territorio, algunos de los cuales
              fueron financiados por el 5G Research en Horizonte 2020. Y gracias
              al despliegue de los principales operadores de telecomunicaciones
              espaoles, Espaa es lder en 5G comercial.
            </p>
          </Content>
        </Modal>
        {/*Modal #5 */}
        <Modal
          value="Calidad de Vida"
          state={calidadDeVida}
          setState={setCalidadDeVida}
        >
          <Content>
            <p>
              Espaa proporciona acceso universal a la sanidad, garantizando a
              los trabajadores desplazados ya los extranjeros prcticamente los
              mismos servicios sanitarios que a los ciudadanos espaoles.
              <br />
              <br />
              El ndice de Competitividad Global del Foro Econmico Mundial
              sita al sistema sanitario espaol en el primer puesto a nivel
              mundial.
              <br />
              <br />
              Espaa es el segundo pas del mundo y el primero de Europa en
              esperanza de vida, segn la OCDE.
            </p>
          </Content>
        </Modal>
      </PorQueEspaaContainer>
    </>
  );
}

export default PorQueEspaa;

const PorQueEspaaContainer = styled.section`
  color: var(--color-secondary);
  text-align: center;
  position: relative;
  z-index: 22;
  h2 {
    padding: 5rem;
    font-size: var(--h2-font-size);
    font-weight: var(--h2-font-weight);
  }
  h2::after {
    content: "";
    display: block;
    width: 8rem;
    height: 4px;
    margin: auto;
    margin-top: 0.7rem;
    background-color: var(--color-primary);
  }
  img {
    height: 8rem;
  }
  h3 {
    font-size: 1.5rem;
  }
  #items {
    display: flex;
    justify-content: center;
    gap: 3.5rem;
    cursor: pointer;
  }

  @media (max-width: 1000px) {
    h2 {
      font-size: var(--h2-font-size-small);
    }
    #items {
      display: grid;
      grid-template-columns: 1fr 1fr !important;
      grid-row-gap: 2rem;
      margin: 0 3rem;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  @media (max-width: 550px) {
    #items {
      flex-direction: column;
      gap: 1rem;
    }
  }
`;

const Content = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;
